.logoutBtn {
  position: relative;
  margin-top: 10px;
}
.logout-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0 0.6rem;
}

.logBtn:hover,
.logBtn:active,
.logBtn::after,
.dropdown-btn:hover,
.dropdown-btn:active,
.dropdown-btn::after {
  text-decoration: none;
}

.dropdown {
  position: absolute;
  top: 35px;
  left: 0px;
  width: 100%;
  box-shadow: none;
  text-align: center;
  visibility: hidden;
  border-radius: 5px;
  border-radius: 10px;
  z-index: 999;
}
.show-dropdown {
  visibility: visible;
}
.dropdown-btn {
  border-radius: 5px;
  margin-top: 10px;
  border-radius: 10px;
  padding: 0.5rem;
  background: transparent;
  border-color: transparent;
  letter-spacing: 5px;
  text-transform: capitalize;
  color: var(--text-color);
  cursor: pointer;
  width: 100%;
  height: 100%;
}
.LogList {
  display: flex;
  justify-content: left;
  align-items: flex-start;
  align-items: flex-start;
  text-align: left;
}
.LogList ul li {
  text-decoration: none;
  list-style-type: none;
  padding: 3px;
  text-transform: capitalize;
  font-size: 18px;
  margin-left: -20px;
  color: var(--text-color);
}
