.registerContainer {
  display: flex;
  position: relative;
  padding: 1.5rem;
  background: var(--form-background);
}
.register-form {
  background: var(--container-color);
  padding: 1.2rem;
  border-radius: 10px;
}
label {
  margin-left: 5px;
  float: left;
}
.formRegister {
  position: absolute;
  vertical-align: middle;
  margin: 0;
  border: solid var(--primary-color);
  border-radius: 0.5rem;
  padding: 1rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--text-color);
}
.error-handler {
  text-align: center;
  max-width: 30vw !important;
}
.formInput {
  padding: 0.3rem;
}
.log {
  display: flex;
  text-align: center !important;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}
.link {
  text-decoration: none;
}
.form-control:focus {
  border-color: none !important;
  box-shadow: 0 0 0 0.2rem rgba(235, 21, 13, 0.285) !important;
}
.submitBtn {
  padding: 1rem;
  margin: 0 auto;
  width: 80%;
}
.submit-btn {
  background: var(--primary-color);
}
.submit-btn:hover {
  background: rgb(238, 52, 89);
}
